import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import {
    Box,
    Container,
    TextField,
    Card,
    Stack,
    Divider,
    Typography,
    Button,
    IconButton,
    InputAdornment
  } from '@mui/material';
import { styled } from '@mui/material/styles';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import { useNavigate } from "react-router-dom";
import { getDoc, doc } from 'firebase/firestore';

const MainContent = styled(Box)(
    ({ theme }) => `
      height: 100%;
      display: flex;
      flex: 1;
      overflow: auto;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  `
  );

function Login() {
    //states
    const [formData, setFormData] = useState({
        email: '',
        password: ''
    });
    const { email, password } = formData;
    const [showPassword, setShowPassword] = useState(false);
    const [errors, setErrors] = useState(null);

    //custom hooks
    const navigate = useNavigate();
    const { loginUser, currentUser, database } = useFirebaseAuth();

    useEffect(() => {
        if (currentUser !== null) {
            getDoc(doc(database, "admin", currentUser.uid)).then((doc) => {
                if (doc.exists()) {
                    navigate('/dashboards/products');
                } else {
                    setErrors('User is not an Admin.');
                }
            }).catch((error) => {
                setErrors('Authentication failed.');
            })
        }
    }, [currentUser, navigate, database]);

    function onChangeValue(e: React.ChangeEvent<HTMLInputElement>) {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    }

    async function onSubmitForm(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        loginUser(email, password).then(result => {
            setErrors(null);
        }).catch(error => {
            setErrors('Email or password is incorrect.');
        });
    }

    return (
        <>
            <Helmet>
                <title>Login</title>
            </Helmet>
            <MainContent>
                <Container maxWidth="sm">
                    <Card sx={{ width: '100%' }}>
                        <form onSubmit={onSubmitForm} noValidate>
                            <Box component="div" 
                                sx={{
                                    display: 'flex',
                                    justifyContent: 'start',
                                    alignItems: 'center',
                                    padding: 3
                                }}
                            >
                                <Typography variant="h2" color="primary">
                                    Login to Butil MNL
                                </Typography>
                            </Box>
                            <Divider />
                            <Stack direction="column" spacing={2} sx={{ padding: 3 }}>
                                <TextField 
                                    name="email" 
                                    type="email"
                                    label="Email" 
                                    variant="outlined"
                                    value={email}
                                    onChange={onChangeValue}
                                    error={errors !== null ? true : false}
                                />
                                <TextField 
                                    type={!showPassword ? "password" : "text"}
                                    name="password" 
                                    label="Password" 
                                    variant="outlined" 
                                    value={password}
                                    onChange={onChangeValue}
                                    error={errors !== null ? true : false}
                                    helperText={errors}
                                    InputProps={{
                                        endAdornment: <InputAdornment position="end">
                                            <IconButton
                                                aria-label="toggle password visibility"
                                                edge="end"
                                                onClick={e => setShowPassword(!showPassword)}
                                                >
                                                    {!showPassword ?  <VisibilityOff /> : <Visibility />}
                                                </IconButton>
                                            </InputAdornment>
                                    }}
                                />
                            </Stack>
                            <Box component="div" 
                                sx={{ padding: 3 }}
                            >
                                <Button 
                                    fullWidth
                                    variant="contained"
                                    type="submit"
                                    sx={{ height: 50 }}
                                >
                                    LOGIN
                                </Button>
                            </Box>
                        </form>
                    </Card>
                </Container>
            </MainContent>
        </>
    )
}

export default Login
